import { darken } from 'polished'

const brand = {
  primary: '#00c5cd',
  secondary: 'orangered',
}

const colors = {
  grey: '#6b6b6b',
  black: '#000',
  white: '#fff',
  bg_color: '#f3f3f3',
  body_color: '#444',
  link_color: brand.primary,
  link_color_hover: `${darken(0.15, brand.primary)}`,
}

const theme = {
  brand,
  colors,
  breakpoints: {
    xxs: '330px',
    xs: '400px',
    s: '750px',
    m: '900px',
    l: '1200px',
    laptop: '1380px',
  },
  container: {
    base: '100rem',
    text: '55rem',
  },
  spacer: {
    horizontal: '2rem',
    vertical: '3rem',
  },
}

export default theme
